@font-face {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular.woff") format("woff");
}
@font-face {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Manrope-Medium.woff2") format("woff2"), url("../fonts/Manrope-Medium.woff") format("woff");
}
@font-face {
  font-family: "Manrope", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Manrope-Bold.woff2") format("woff2"), url("../fonts/Manrope-Bold.woff") format("woff");
}
html,
body {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

body {
  position: relative;
  max-height: 100%;
  width: 100%;
  font-size: 14px;
  line-height: 30px;
  font-family: "Manrope", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
  max-width: 1440px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.navigation {
  display: flex;
  justify-content: end;
  align-items: center;
}

.navigation__ul {
  display: flex;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0;
  list-style: none;
}

.navigation__li {
  display: flex;
  align-items: center;
  min-height: 48px;
  margin-left: 35px;
  cursor: pointer;
  user-select: none;
}
.navigation__li:first-child {
  margin-left: 0;
}

.navigation__li a {
  color: #38393c;
  text-decoration: none;
}

.navigation__li a::before,
.navigation__li a::after {
  display: inline-block;
  opacity: 0;
  transition: transform 0.3s, opacity 0.2s;
}

.navigation__li a::before {
  margin-right: 10px;
  content: "[";
  transform: translateX(20px);
}

.navigation__li a::after {
  margin-left: 10px;
  content: "]";
  transform: translateX(-20px);
}

.navigation__li a:hover::before,
.navigation__li a:hover::after,
.navigation__li a:focus::before,
.navigation__li a:focus::after {
  opacity: 1;
  transform: translateX(0px);
}